.find-room
  max-width: 380px
  background-color: #fff
  border: 1px solid rgba(0, 0, 0, 0.12)
  box-sizing: border-box
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2)
  border-radius: 4px
  padding: 30px
  padding-top: 40px

  &__btn
    margin-top: 30px

  &__item
    margin-top: 19px

  &__title
    @include h1

  &__group-field
    @include group-field

  &__field
    @include field
